.modal {
  position: fixed;
  top: 0;
  left: 0;
  width:100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
}

.modal-main {
  position:fixed;
  background: white;
  width: 80%;
  height: auto;
  top:50%;
  left:50%;
  transform: translate(-50%,-50%);
}

.display-block {
  display: block !important;
}

.display-none {
  display: none;
}

.modalText{
  margin-top: 15px;
  color: #000000;
}

.modalTitle{
  color: #000000;
  text-align: left;
  font-size: 15px;
}

.modalInput{
  font-size: 20px;
}

